import { AlertService } from 'src/app/services/alert.service';
import { ApiCRMService } from 'src/app/services/api-crm.service';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { Component, OnInit } from '@angular/core';
import { ApiGeneralService } from 'src/app/services/api-general.service';
import { DomSanitizer } from '@angular/platform-browser';
import { ToastService } from 'src/app/services/toast.service';
import { environment } from 'src/environments/environment.prod';


@Component({
  selector: 'app-mbbs-foundation-course',
  templateUrl: './mbbs-foundation-course.page.html',
  styleUrls: ['./mbbs-foundation-course.page.scss'],
})
export class MbbsFoundationCoursePage implements OnInit {

  isLoading: boolean;

  //#region Video Variable
  webAssetsPath = "";
  arrVideos = [];
  iframe : any;
  isVideoOpen : boolean ;
  slideVideoOpts = {
    slidesPerView: 1.5    
  }
//#endregion
  constructor(
    private headerTitleService : HeaderTitleService,
    private apiGeneralService : ApiGeneralService,
    private sanitizer : DomSanitizer,
    private toastr : ToastService,
    private apiCRMService : ApiCRMService,
    private alertService : AlertService
  ) {
    headerTitleService.title="MBBS Foundation Course";
    headerTitleService.isBack= true;
  }

  ngOnInit() {
    this.getVideos();    
  }
  onClickRegister(){
    this.setCRMRawData();
  }
  //#region CRM Update
  setCRMRawData(){
    this.isLoading = true;
    this.apiCRMService.fnGetCRMLeadData().then(resp=>{
      if(resp == true){
        this.apiCRMService.arrCRMRawData["lmsCourse"] = this.apiCRMService.enumLMSCourses[0];
        this.apiCRMService.fnSetCRMLeadData().then((arrLeadData:any)=>{
          if(arrLeadData!= 0){
            this.alertService.basciAlert("Thank you for Registration", "We have received your request. Our counsellor will call you shortly","",['OK']);
          }
          else{
            this.alertService.basciAlert("Registration failed. Please contact our support team","","",['OK']);
          }
          this.isLoading = false;
        });
      }
      else{
        this.isLoading = false;
        this.alertService.basciAlert("Sorry, we are unable to proceed your request at this time.","","",['OK']);
      }
    });
  }  
  //#endregion

  //#region  Video Section start
  async getVideos() {
    let postData = { 'videoHeaderId': 32 }
    await this.apiGeneralService.getVideos(JSON.stringify(postData)).then(resp => {
      this.webAssetsPath = environment.webAssetsPath + "mobility-app/videothumbnails";
      this.arrVideos = resp;
    })
      .catch(err => {
        console.log("getVideos: " + err);
      })
  }
  
  playVideo(videoSrc){
    if(videoSrc == ""){
      this.toastr.toastError("Sorry! Currently this video is not available.");
      return false;
    }
    this.iframe = this.sanitizer.bypassSecurityTrustHtml('<iframe src="'+videoSrc+'" frameborder="0" style="width: 100%; height: 300px;"allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>');    
    this.isVideoOpen = true;
  }
  videoPanelClose(){
    this.iframe = "";
    this.isVideoOpen = false;
  }
  //#endregion

}
